.user-meta {
  position:   relative;
  padding:    0.3rem 40px 0 100px;
  min-height: 77px;
}

.user-image {
  position: absolute;
  top:      0;
  left:     0;
}

.user-name {
  display:     block;
  font-weight: 700;
}

.user-bio {
  display:     block;
  max-width:   440px;
  font-size:   1.4rem;
  line-height: 1.5em;
}
